import React, { Component } from 'react'
import { fact2chart } from '@/tool/fact2vis/fact2vis'
import { genFactSentence } from '@/sentencer/index'
import { hightlight } from '@/tool/helper'
import * as d3 from 'd3'
import _ from 'lodash';
import './AnsweredFacts.less'


let testLayout = [1, 2, 3, 4, 5]
const defaultNum = testLayout[4]

const visSmall = {
    // width: '235',
    width: '200',
    height: '150'
}

let visNodes = new Map()
let captionNodes = new Map()
const maxNum = 5;
let renderLayout = null
let debounceTimer = null
export default class AnsweredFacts extends Component {

    layoutNode = React.createRef()

    constructor(props) {
        super(props);
        this.state = {
            renderNum: defaultNum,
            count: maxNum,
            clientWidth: 0,
            clientHeight: 0,
            facts: this.props.facts
        }
        this.visBox = React.createRef()
        this.TreemapSize = new Array(this.props.columnNum).fill({
            preWidth: 0,
            preHeight: 0
        })
    }
    componentDidMount() {
        this.updateTreeMapLayout()
        this.onResize()
    }

    componentDidUpdate() {
        this.updateTreeMapLayout()
    }

    onResize = () => {
        window.addEventListener('resize', () => {
            if (debounceTimer) clearTimeout(debounceTimer)
            setTimeout(() => {
                this.updateTreeMapLayout()
            }, 300)
        })
    }

    updateTreeMapLayout = () => {
        //updateTreeMapLayout    
        if (this.layoutNode.current) {
            const { clientWidth, clientHeight } = this.layoutNode.current
            const { QAID } = this.props
            let { preWidth, preHeight } = this.TreemapSize[QAID]

            if ((clientWidth !== 0 && preWidth !== clientWidth) || (preHeight !== clientHeight && clientHeight !== 0)) {
                this.forceUpdate()
                this.updateTreemapSize(QAID, clientWidth, clientHeight) //update cache
            }
        }
        this.batchUpdateVis()
        this.svgTransformer()
    }

    updateTreemapSize = (id, newWidth, newHeight) => {
        this.TreemapSize[id].preWidth = newWidth
        this.TreemapSize[id].preHeight = newHeight
    }
    batchUpdateVis = () => {
        let { facts } = this.props
        facts.map((fact, index) => {
            if (!visNodes.get(fact.id) || !captionNodes.get(fact.id)) return fact

            let visNode = visNodes.get(fact.id).current
            let captionNode = captionNodes.get(fact.id).current
            let visWidth = 0, visHeight = 0
            let captionH = 0;

            if (captionNode) {
                captionH = captionNode.clientHeight
            }
            if (renderLayout && renderLayout[index]) {
                let { x0, x1, y0, y1 } = renderLayout[index]
                visWidth = x1 - x0
                visHeight = y1 - y0 - captionH
            }
            if (visWidth === 0 || visHeight === 0) return fact

            if (visNode) {
                this.svgTransformer(visNode, visWidth, visHeight)
            }
            this.updateAVis(fact, visWidth, visHeight, captionH)
            return fact
        })
    }

    svgTransformer = (visNode, visWidth, visHeight) => {
        if (visNode) {
            // let scaleX = visWidth / 640,
            //     scaleY = visHeight / 640;
            let scale = Math.min(visWidth, visHeight) / 640
            let marginRotio = 0.9
            // console.log(_node, scaleX, scaleY);
            let visChart = visNode.children && visNode.children[0]
            //visChart && visChart.setAttribute("style", `transform:scale(${scaleX * marginRotio},${scaleY * marginRotio})`)
            if (visChart) {
                visChart.setAttribute("style", `transform:scale(${scale * marginRotio})`)
                visChart.onmouseenter = () => {
                    const { isEdit } = this.props
                    visChart.setAttribute("style", `transform:scale(${scale * marginRotio});border:${isEdit ? '1px dashed grey' : ''}`)
                }
                visChart.onmouseleave = () => {
                    visChart.setAttribute("style", `transform:scale(${scale * marginRotio})`)
                }
            }
        }
    }

    _isValidate = (visWidth, visHeight) => {
        if ((visWidth && visWidth < visSmall.width) || (visHeight && visHeight < visSmall.height)) {
            return false
        }
        return true
    }
    _renderedFacts = (facts) => {
        const { renderNum, count } = this.state
        return facts.slice(0, renderNum >= count ? count : renderNum)
    }

    updateAVis = (cachedFact, visWidth, visHeight, visCaptionH) => {
        let { width, height, captionH } = cachedFact //get by cache
        //console.log("updateAVis", cachedFact.id, width, visWidth, height, visHeight);

        if (width !== visWidth || height !== visHeight || captionH !== visCaptionH) {
            const { facts } = this.props
            let theFact = facts[facts.findIndex(d => d.id === cachedFact.id)]
            theFact.width = visWidth
            theFact.height = visHeight
            theFact.captionH = visCaptionH
            //console.log(cachedFact.id, visWidth, visHeight, visCaptionH);

            this.setState({
                facts
            })
        }
    }

    weightAdapter = (data, columnNum) => {
        let innerNum = data.children.length
        let weightTable = []
        switch (columnNum) {
            case 1:
                weightTable = [1]
                if (innerNum === 2) {
                    weightTable = [3, 2]
                } else if (innerNum === 3) {
                    weightTable = [3, 1.2, 0.8]
                }
                break;
            case 2:
                weightTable = [1]
                if (innerNum === 2) {
                    weightTable = [4, 3]
                } else if (innerNum === 3) {
                    weightTable = [6, 3, 2]
                }
                break;
            case 3:
                weightTable = [1]
                if (innerNum === 2) {
                    weightTable = [3, 2]
                } else if (innerNum === 3) {
                    weightTable = [3, 1.2, 0.8]
                }
                break;
            case 4:
                weightTable = [1]
                if (innerNum === 2) {
                    weightTable = [3, 2]
                } else if (innerNum === 3) {
                    weightTable = [4, 3, 2]
                }
                break;
            default:
                break;
        }
        return {
            "children": data.children.map((fact, index) => {
                return {
                    ...fact,
                    significance: weightTable[index]
                }
            })
        }
    }

    change = (event) => {
        this.setState({
            renderNum: event.target.value
        })
    }

    getTreemap = (width, height, data) => {
        let treemap = d3.treemap()
            .tile(d3.treemapResquarify.ratio(1))
            .size([width, height])
            .padding(3)
            .round(true)
            (d3.hierarchy(data)
                .sum(d => d.significance)
            )
        return treemap
    }

    sort = (data) => {
        return data.sort((a, b) => b.significance - a.significance)
    }

    getSize = () => {
        return "large"
    }

    setRef = (id) => {
        visNodes.set(id, React.createRef())
        return visNodes.get(id)
    }
    setCaptionRef = (id) => {
        captionNodes.set(id, React.createRef())
        return captionNodes.get(id)
    }
    endEdit = (visId, fact, index) => {
        let captionNode = captionNodes.get(visId).current
        const { cachedQA } = this.props
        let QAId = parseInt(fact.id.split('QA')[0])
        cachedQA[QAId].facts[index].script = captionNode.innerHTML
        this.props.saveCachedDecomposedQA(cachedQA)
    }

    deleteCard = (fact, index) => {
        let { cachedQA } = this.props
        let newCachedQA = _.cloneDeep(cachedQA)
        let parentId = parseInt(fact.id.split('QA')[0])
        newCachedQA[parentId] && newCachedQA[parentId].facts.splice(index, 1)
        if (newCachedQA[parentId] && newCachedQA[parentId].facts.length === 0) {
            newCachedQA.splice(parentId, 1)
        }
        this.props.saveCachedDecomposedQA(newCachedQA)
    }

    render() {

        let { facts, data, schema, columnNum, isEdit, QAID } = this.props
        let { preWidth, preHeight } = this.TreemapSize[QAID] ? this.TreemapSize[QAID] : { preWidth: 0, preHeight: 0 }

        let renderData = {
            children: this._renderedFacts(this.sort(facts))
        }

        renderLayout = this.getTreemap(preWidth, preHeight, this.weightAdapter(renderData, columnNum)).leaves()

        const LayoutView = ({ index, fact, x, y, width, height }) => {

            let vegalite = {
                // type: "vegalite",
                // height: fact.height - 100,
                // width: fact.width - 100,
                height: 640,
                width: 640,
            }

            let script = fact.script ? fact.script : genFactSentence(fact) || " "
            fact.generatedScript = script

            return (
                <div style={{ position: 'absolute', left: x, top: y, width: width, height: height }}>
                    <div className='chart-card'>
                        {
                            isEdit && <div className='delete' onClick={() => this.deleteCard(fact, index)}></div>
                        }
                        <div className='wrapper'>
                            <div className='vis-box' style={{ height: `calc(100% - ${fact.captionH}px)` }}>
                                <div className='bound-box' id={fact.id} ref={this.setRef(fact.id)}>
                                    {
                                        fact2chart({ schema }, fact.id, fact, data, this.getSize(), { ...vegalite })
                                    }
                                </div>
                            </div>
                            <div className='caption' contentEditable={isEdit} suppressContentEditableWarning ref={this.setCaptionRef(fact.id, index)} onBlur={() => this.endEdit(fact.id, fact, index)}
                                //title={script}
                                dangerouslySetInnerHTML={{ __html: hightlight(fact) }}
                            ></div>
                        </div>
                    </div>
                </div >)
        }


        return (
            <div className='inner-auto-layout' ref={this.layoutNode} >
                {
                    renderLayout && renderLayout.map((layout, key) => {
                        return <LayoutView
                            key={key}
                            index={key}
                            fact={layout.data}
                            x={layout.x0}
                            y={layout.y0}
                            width={layout.x1 - layout.x0}
                            height={layout.y1 - layout.y0}
                        />
                    })
                }
            </div >
        )
    }
}